import BannerImage from "../img/banner.png";
import "../styles/Contact.css"
function Contact() {
  return <div className="contact">
    <div className="leftSide" style={{backgroundImage:`url(${BannerImage})`}}>

    </div>
    <div className="rightSide">
<h1>Bizimle letiime Gein</h1>
<form >
    <label>Ad Soyad</label>
    <input type="text" name="name" placeholder="Ltfen Adnz Soyadnz giriniz..." />
    <label>E-mail</label>
    <input type="email" name="email" placeholder="Ltfen mailinizi giriniz..." />
    <label>Mesajnz</label>
    <textarea rows={6} name="message" placeholder="Ltfen mesajnz giriniz..." />
</form>
    </div>
  </div>;
}
export default Contact;
